#submit-btn{
    background-color: #db2b2b;
    color: white;
    font-size: 18px;
    font-weight: bold;
    border: none;
    padding: 14px;
    width: 14rem;
    border-radius: 20px;
    cursor: pointer;
}

@media screen and (max-width: 500px) {
    #submit-btn{
        width: 85%;
    }
}